import { useCallback } from "react";
import { useTranslation } from "react-i18next";
import { Plus } from "lucide-react";
import { Button } from "@/components/ui/button";
import { Skeleton } from "@/components/ui/skeleton";
import PostGridCard from "@/components/community/PostGridCard";
import CreatePostDialog from "@/components/community/CreatePostDialog";
import { useCommunityPosts, useCanCreatePost } from "@/hooks/useCommunityPosts";
import { PullToRefresh } from "@/components/PullToRefresh";
import { useQueryClient } from "@tanstack/react-query";

// Skeleton for grid card
const PostGridSkeleton = () => <div className="rounded-xl overflow-hidden bg-card border border-border/50">
    <Skeleton className="aspect-[4/5] w-full" />
    <div className="p-3 space-y-3">
      <div className="space-y-2">
        <Skeleton className="h-4 w-full" />
        <Skeleton className="h-4 w-3/4" />
      </div>
      <div className="flex items-center justify-between">
        <div className="flex items-center gap-2">
          <Skeleton className="h-5 w-5 rounded-full" />
          <Skeleton className="h-3 w-16" />
        </div>
        <Skeleton className="h-4 w-10" />
      </div>
    </div>
  </div>;
const Community = () => {
  const {
    t
  } = useTranslation();
  const queryClient = useQueryClient();
  const {
    data: posts,
    isLoading
  } = useCommunityPosts({
    status: "approved"
  });
  const {
    data: canPost
  } = useCanCreatePost();
  const handleRefresh = useCallback(async () => {
    await queryClient.invalidateQueries({
      queryKey: ['community-posts']
    });
  }, [queryClient]);
  return <PullToRefresh onRefresh={handleRefresh}>
      <div className="min-h-screen bg-secondary/20 py-8 px-4 animate-fade-in">
        <div className="container mx-auto max-w-4xl">
          {/* Header */}
          <div className="flex items-center justify-between mb-6">
            <div>
              <h1 className="text-3xl sm:text-4xl mb-2 text-primary font-medium">
                {t("community.title", "ç¤¾åŒº")}
              </h1>
              <p className="text-muted-foreground text-base sm:text-lg">
                {t("community.description", "åˆ†äº«ç¾é£Ÿä½“éªŒï¼Œå‘ç°æ›´å¤šç²¾å½©")}
              </p>
            </div>
            {canPost && <CreatePostDialog trigger={<Button size="sm" className="gap-1.5">
                    <Plus className="h-4 w-4" />
                    <span className="hidden sm:inline">{t("community.createPost", "å‘å¸ƒ")}</span>
                  </Button>} />}
          </div>

        {/* Content */}
        {isLoading ?
        // Loading skeleton - masonry style
        <div className="columns-2 sm:columns-3 lg:columns-4 xl:columns-5 gap-3 sm:gap-4">
            {Array.from({
            length: 6
          }).map((_, i) => <div key={i} className="break-inside-avoid mb-3 sm:mb-4">
                <PostGridSkeleton />
              </div>)}
          </div> : posts && posts.length > 0 ?
        // Xiaohongshu-style Masonry Grid
        <div className="columns-2 sm:columns-3 lg:columns-4 xl:columns-5 gap-3 sm:gap-4">
            {posts.map(post => <div key={post.id} className="break-inside-avoid mb-3 sm:mb-4">
                <PostGridCard post={post} />
              </div>)}
          </div> :
        // Empty state - separate container for proper centering
        <div className="flex flex-col items-center justify-center min-h-[60vh] text-center px-4">
            <div className="w-20 h-20 rounded-full bg-muted/50 flex items-center justify-center mb-4">
              <Plus className="w-8 h-8 text-muted-foreground/50" />
            </div>
            <p className="text-muted-foreground text-lg">{t("community.noPosts", "æš‚æ— å¸–å­")}</p>
            {canPost && <p className="mt-2 text-sm text-muted-foreground/70">
                {t("community.beFirst", "æˆä¸ºç¬¬ä¸€ä¸ªå‘å¸–çš„äººå§ï¼")}
              </p>}
          </div>}
        </div>
      </div>
    </PullToRefresh>;
};
export default Community;